@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&display=swap');

.App {
  text-align: center;
  background-color: #2a2d36;
  height: 100vh;
  width: 100vw;
  font-family: "Oswald", sans-serif;
  overflow-x: hidden;
  background-image: radial-gradient(circle, rgba(89, 89, 89, 1) 0%, rgba(20, 20, 20, 1) 100%);
 
  main {
    margin-top: 10vmin;
    color: white;
      
      header {
        padding-top: calc(20px + 4vmin);
        padding-bottom: calc(10px + 0vmin);
        background-color: none;
        
        h1 {
          font-size: calc(1rem + 3vmin);
          font-weight: 400;
          animation: pulsate 2.5s infinite alternate; 
         
          .underline {
            border-bottom: 1px solid white;
            // animation: pulsate 2.5s infinite alternate; 
          }
        }
      }
       
        section {
          display: flex;
          align-items: center;
          justify-content: space-evenly;  
          flex-wrap: wrap;

          .block-1, .block-2, .block-3 {
            width: 30vw;
            height: 50vmin;
            font-size: calc(8px + 1vmin);
            @media screen and (max-width: 960px) {
                width: 48vw;
                height: 25vh;
                min-height: 20vh;
                h3{
                  font-size: calc(12px + 1vmin);
                }
              }
              @media screen and (max-width: 420px) {
                height: 80px;    
                margin-top: 4vmin;
              }
          }

          .block-3 {
            font-weight: lighter;

                @media screen and (max-width: 960px) {
                  width: 92vw;
                  overflow-y: visible;
                  margin-top: 7vmin;
                }
             } 
     
            @media screen and (max-width: 420px) {
              overflow-y: unset;
              flex-direction: column;
              margin-top: -7vmin;
            }
       }
    }
}

@keyframes pulsate {
  100% {
    text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #ffa500,
      0 0 80px #ffa500,
      0 0 90px #ffa500,
      0 0 100px #ffa500,
      0 0 150px #ffa500;
  }
  0% {
    text-shadow:
      0 0 2px #fff,
      0 0 4px #fff,
      0 0 6px #fff,
      0 0 10px #ffa500,
      0 0 45px #ffa500,
      0 0 55px #ffa500,
      0 0 70px #ffa500,
      0 0 80px #ffa500;
  }
}
