@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&display=swap');

.footer {
    width: 100vw;
    height: calc(40px + 9vmin);
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #15161a;
    padding: 0;
    margin: 0;

    .foot-container {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        place-items: center; 
       
        .css-logo{
            display: flex;
            justify-self: start;
            margin-left: calc(5vmin);
            cursor: pointer;
            text-align: left;
            text-decoration: none;
            font-size: calc(8px + 1.4vmin);
            align-self: center;
            color: #706f6f;
            transition: all 1s ease;

            &:hover{
                color: white;
            }

            .square{
                position: absolute;
                width: calc(30px + 7vmin);
                height: calc(30px + 7vmin);
                background-color: transparent;
                border: #4b4b4b 1px solid;
                transition: all .5s ease;

                &:hover{
                    border: orange 1px solid;
                    transform: rotate(180deg);
                    box-shadow:
                        0 0 .2rem #fff,
                        0 0 2rem #ffa500,
                        0 0 0.8rem #ffa500,
                        0 0 2rem #ffa500,
                        inset 0 0 1.3rem #ffa500;
                }
            }

            @media screen and (max-width: 520px) {
                display: none;
            }
        }        
        
        .koa {
            position: absolute;
            right: 50%;
            bottom: 0;
            height: calc(12px + 3vmin);
            width: calc(12px + 3vmin);
            line-height: calc(12px + 3vmin);
            font-size: calc(2px + 1vmin);
            background-color: rgb(85, 85, 85);
            color: orange;
            border-top-left-radius: 8px;
            border-top-right-radius: 8px;
            text-align: center;
            box-shadow: inset 2px 2px 8px 0px black;

            @media screen and (max-width: 660px) {
                        display: none;
                    }
        }

        .dropup {
            width: 240px; 
            position: absolute;
            display: flex;
            right: 60px;
            font-family: "Oswald", sans-serif;

            .footlabel {
                background-color : inherit; 
                color : #bebebe;
                cursor : pointer;
                position: relative;
                width: 240px;
                border: 1px solid #2a2d34;
                border-radius: 3px;
                height: 48px;
                display: flex;
                justify-content: center;
                justify-self: end;
                align-items: center;     
                font-weight: 600;
                font-size: 1.1rem;
                margin: 0;

                &:after {
                    content: "";
                    margin-left: 15px;
                    transform: rotate(-135deg);
                    border: solid #bebebe;
                    border-width: 0 2px 2px 0;
                    display: inline-block;
                    padding: 4px;
                    margin-top: 3px;
                    transition: transform .2s;
                    }           

                &:hover {
                    color: white;
                    border-color: white;
                    &:after{
                        border-color: white;
                        }
                    }
                }
              
              .slide {
                position: fixed;
                display: block;
                width: 240px;
                height: 0px;
                overflow: hidden;
                text-align: center;
                transition: height .4s ease;
                bottom: calc(40px + 9vmin);
                background-color: lightgray;
                box-shadow: rgba(0, 0, 0, 0) 0px 54px 55px, rgba(0, 0, 0, 0.2) 0px -12px 30px, rgba(0, 0, 0, 0.2) 0px 4px 6px, rgba(0, 0, 0, 0.23) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -5px 5px;
                font-family: "Oswald", sans-serif;
                
                button {
                    color: rgb(50, 50, 50);
                    background-color: inherit;
                    display: block;
                    border: none;
                    font-weight: 600;
                    font-size: 16px;
                    margin: 14px; 
                    font-family: "Oswald", sans-serif;   

                     i  {
                        transform: rotate(-45deg);
                        border: solid black;
                        border-width: 0 2px 2px 0;
                        display: inline-block;
                        padding: 4px;
                        margin-right: 6px; 
                        }

                    &:hover {
                        color: #e58f2c;
                        i {
                            border: solid #e58f2c;
                            border-width: 0 2px 2px 0;
                          }
                        }         
                    }                          
                }             

            #mark {
                position: absolute;
                opacity: 0; 
                height: 0;
                }  

            #mark:checked ~ .slide  {
                height: 90px; 
                }  
                    
            #mark:checked ~ .footlabel:after  {
                margin-top: -3px;
                transform: rotate(45deg);
                }       
        }    
    }
}