.topbar {
    width: 100%;
    height: calc(50px + 8vmin);
    position: fixed;
    top: 0;
    background-color: #0b0c10;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;

    .top-container {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .logo-circle {
            display: flex;
            cursor: pointer;
            text-decoration: none;
            margin-left: clamp(1.875rem, 1.193rem + 3.41vw, 3.75rem);
            background-color: #303030;
            border-radius: 50%;
            height: calc(40px + 6vmin);
            width: calc(40px + 6vmin);
            align-items: center;
            justify-content: center;
            transition: all .8s ease;

            .html-icon {
                margin-top: 1vmin;
                color: white;
                width: calc(30px + 5vmin);
                height: calc(30px + 5vmin);
            }

            &:hover {
                background-color: #feb329a9;
                // animation: pulsate 2.5s infinite alternate;
                 box-shadow:
                        0 0 .2rem #fff,
                        0 0 2rem #feb329,
                        0 0 0.8rem #feb329,
                        0 0 2.4rem #feb329,
                        inset 0 0 1.3rem #feb329;
            }
            }

        
        h1{
            margin-left: auto;
            margin-right: 4vw;
            font-size: calc(14px + 2vmin);
            text-align: right;
        }
    }
    
    @media screen and (max-width: 960px) {
        & {
            overflow: hidden;
        }   
    }
}

@keyframes pulsate {
    100% {
      text-shadow:
        0 0 4px #fff,
        0 0 11px #fff,
        0 0 19px #fff,
        0 0 40px #feb329,
        0 0 80px #feb329,
        0 0 90px #feb329,
        0 0 100px #feb329,
        0 0 140px #feb329;
    }
    0% {
      text-shadow:
        0 0 2px #fff,
        0 0 4px #fff,
        0 0 6px #fff,
        0 0 10px #feb329,
        0 0 45px #feb329,
        0 0 55px #feb329,
        0 0 70px #feb329,
        0 0 80px #feb329;
    }
  }