    label {
    margin: 1rem;
    display:flex;
    align-items: center;
    font-weight: 300;
    font-size: calc(12px + 1vmin);
    height: 5vmin;
     
        input[type='radio'] {
            border: 3px solid white;
            appearance:none;
            border-radius: 50%;
            width: calc(20px + 1vmin);
            height: calc(20px + 1vmin);
            margin: calc(20px + 2vmin);     
            // height: 24px;
            // width: 24px;
            margin-left: calc(5px + 1vmin);
            position: relative;

            &:checked:after {
                content: '';
                width: calc(12px + 0.4vmin);
                height: calc(12px + 0.4vmin);
                // height: 12px;
                // width: 12px;
                background-color: white;
                border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
   
    @media screen and (max-width: 960px) {
        width: 48vw;
        height: 5vmin;
       }    
}
