@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&display=swap');

.blockTwo{

    margin:auto;
    margin-top: 4vmin;
  
    button {
        width: calc(60px + 10vmin);
        height: calc(20px + 3vmin);
        margin: clamp(0.2vmin, 20px, 2vmin);
        background-color: transparent;
        border: 1px solid white;
        border-radius: 5px;
        color : #bebebe;
        font-size: 1rem;
        font-weight: 700;
        cursor: pointer;
        font-family: "Oswald", sans-serif;
        transition: all 1.5s;

        &:hover {
            background-color: #feb3295e;
            color: #feb329;
            text-shadow: 0 0 2px #feb329, 0 0 5px #feb329;
            animation: puls 2.5s infinite alternate; 
        }
    }
}

@keyframes puls {
  100% {
    box-shadow:
      0 0 2px #fff,
      0 0 5px #fff,
      0 0 9px #fff,
      0 0 20px #feb329,
      0 0 40px #feb329,
      0 0 45px #feb329,
      0 0 50px #feb329,
      0 0 75px #feb329;
  }
  0% {
    box-shadow:
      0 0 1px #fff,
      0 0 5px #feb329,
      0 0 22px #feb329,
      0 0 27px #feb329,
      0 0 35px #feb329,
      0 0 40px #feb329;
  }
}
